.header__logo:hover,
.header__logo:focus,
.sidebar-hero__control-link:hover,
.footer__social-link:hover,
.sidebar-hero__social-link:hover,
.nav__link:hover,
.header-burger:hover,
.sidebar-hero__control-link:focus,
.footer__social-link:focus,
.sidebar-hero__social-link:focus,
.nav__link:focus,
.header-burger:focus {
  color: #FF6C00;
}

.sidebar-hero__control-link,
.footer__social-link,
.sidebar-hero__social-link,
.nav__link,
.header-burger {
  text-decoration: none;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.footer__social-link,
.sidebar-hero__social-link,
.nav__link,
.header-burger {
  display: inline-block;
  padding-top: 10px;
  padding-bottom: 10px;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.363;
}

.footer__social-list,
.footer__box,
.footer__copyright,
.contacts__link,
.booking-form__required,
.contact__label,
.contact > .container,
.gallery__list,
.our-masters__social-list,
.our-masters__list,
.feature__list,
.feature > .container,
.price__button-wrapper,
.price__item,
.price__list-wrapper,
.sidebar-hero__controls,
.sidebar-hero__social-list,
.sidebar-hero,
.about__media-list,
.about > .container,
.mobile-button,
.nav__list,
.header__contacts {
  display: flex;
}

.footer__social-list,
.contacts__list,
.gallery__list,
.our-masters__social-list,
.our-masters__list,
.feature__list,
.price__list,
.sidebar-hero__controls,
.sidebar-hero__social-list,
.about__media-list,
.mobile-menu__list,
.modal-form__option-list,
.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.contact__title,
.our-masters__title,
.feature__title,
.price__title,
.about__title {
  margin-top: 0px;
  margin-bottom: 20px;
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 42px;
  line-height: 1.166;
  letter-spacing: 0.05em;
  color: #303030;
}

.gallery__subtitle,
.our-masters__subtitle,
.feature__subtitle,
.price__subtitle,
.sidebar-hero__subtitle,
.about__subtitle,
.modal-form__legend {
  display: flex;
  align-items: center;
  margin-top: 0px;
  margin-bottom: 20px;
  font-weight: 600;
  font-size: 11px;
  line-height: 1.363;
  text-transform: uppercase;
}

.gallery__subtitle::before,
.our-masters__subtitle::before,
.feature__subtitle::before,
.price__subtitle::before,
.sidebar-hero__subtitle::before,
.about__subtitle::before,
.modal-form__legend::before {
  content: "";
  display: inline-block;
  margin-right: 20px;
  width: 60px;
  height: 1px;
  background: currentColor;
}

.feature__description,
.sidebar-hero__description,
.about__description {
  margin-top: 0px;
  margin-bottom: 20px;
  font-size: 18px;
  line-height: 1.666;
}

.feature__content,
.feature__list,
.about__content,
.about__media-item,
.about__media-wrapper {
  width: calc((100% - 30px) / 2);
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}

.container {
  max-width: 1200px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto;
}

.link {
  text-decoration: none;
  color: inherit;
  transition: color 0.3s ease;
}

.section {
  padding-top: 120px;
  padding-bottom: 120px;
}

.flexbox {
  display: flex;
}

.button {
  min-width: 160px;
  padding: 12px 20px;
  font-family: "Open Sans", sans-serif;
  font-weight: 600;
  font-size: 12px;
  line-height: 1.33;
  letter-spacing: 0.09em;
  text-transform: uppercase;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  color: #FFFFFF;
  border: solid 1px #FF6C00;
  border-radius: 25px;
  outline: none;
  transition: 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.button--black {
  color: #303030;
}

.button:hover,
.button:focus {
  background-color: #FF6C00;
}

@keyframes neon {
  0% {
    text-shadow: 0 0 6px rgba(255, 255, 255, 0.92), 0 0 30px rgba(255, 255, 255, 0.34), 0 0 12px rgba(242, 243, 243, 0.52), 0 0 21px rgb(255, 108, 0), 0 0 34px rgba(242, 243, 244, 0.78), 0 0 54px rgba(242, 243, 244, 0.92);
  }

  100% {
    text-shadow: 0 0 6px rgba(255, 255, 255, 0.98), 0 0 30px rgba(255, 255, 255, 0.42), 0 0 12px rgba(242, 243, 244, 0.58), 0 0 22px rgba(242, 243, 244, 0.84), 0 0 38px rgba(242, 243, 244, 0.88), 0 0 60px #f2f3f4;
  }
}

