body {
  background-color: #FFFFFF;
  color: #555555;
  font-family: "Open Sans", sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 1.625;
  letter-spacing: 0.02em;
}

body.no-scroll {
  overflow: hidden;
  position: fixed;
  width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
ul {
  margin: 0;
  padding: 0;
}

p {
  margin-top: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

fieldset {
  border: none;
  padding: 0;
  margin: 0;
}

legend {
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 12px;
}

input:-webkit-autofill,
textarea:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 1000px #ffffff inset;
  box-shadow: 0 0 0 1000px #ffffff inset;
  -webkit-text-fill-color: #000 !important;
  transition: background-color 5000s ease-in-out 0s;
}

.header--fixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99;
}

.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 20px 15px;
  gap: 15px;
  background-color: rgba(25, 28, 38, 0.2);
  transition: background-color 0.3s ease, backdrop-filter 0.3s ease;
}

.header__wrapper--scrolled {
  background-color: rgba(25, 28, 38, 0.5);
  backdrop-filter: blur(4px);
}

.header__logo {
  display: inline-block;
  width: 69px;
  height: 56px;
  margin-right: 130px;
  color: #FFFFFF;
  transition: color 250ms cubic-bezier(0.42, 0, 0.58, 1);
}

.header__nav {
  margin-right: auto;
}

.header__contacts {
  gap: 40px;
  justify-content: space-between;
}

.header-burger {
  display: none;
  align-items: center;
  color: #FFFFFF;
}

.header-burger .header-burger__text {
  margin-right: 10px;
  font-size: 18px;
}

.header-burger span {
  display: block;
  width: 28px;
  height: 3px;
  background-color: #FF6C00;
  margin-bottom: 5px;
}

.nav__list {
  gap: 30px;
}

.nav__link {
  color: #FFFFFF;
}

.logo__icon {
  fill: currentColor;
}

.logo--black {
  color: #303030;
}

.modal-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  visibility: visible;
  opacity: 1;
  background-color: rgba(25, 28, 38, 0.5);
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1), visibility 250ms cubic-bezier(0.4, 0, 0.2, 1);
  overflow: hidden;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.modal-backdrop.is-hidden .modal {
  transform: translate(-50%, -50%) scale(0.7);
}

.modal {
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%) scale(1);
  max-width: 500px;
  min-height: 700px;
  padding: 35px 15px 35px 35px;
  background-color: #FFFFFF;
  border-radius: 5px;
  box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #9da4bd;
  border-radius: 50%;
  cursor: pointer;
  transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__close-btn:hover,
.modal__close-btn:focus {
  border-color: #FF6C00;
}

.modal__close-btn:hover .modal__icon,
.modal__close-btn:focus .modal__icon {
  fill: #FF6C00;
}

.modal__icon {
  fill: #555555;
  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal__title {
  margin-top: 0;
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: 600;
  line-height: 1.5;
  letter-spacing: 0.05em;
  color: #303030;
  text-align: center;
}

.modal-form {
  max-height: 600px;
  overflow-y: auto;
  padding-right: 15px;
  padding-top: 15px;
}

.modal-form::-webkit-scrollbar {
  width: 5px;
  max-height: 25px;
}

.modal-form::-webkit-scrollbar-track-piece {
  background-color: #f2f3f8;
}

.modal-form::-webkit-scrollbar-thumb {
  height: 5px;
  width: 5px;
  background-color: #FFA64D;
  border-radius: 10px;
}

.modal-form__field {
  position: relative;
  display: block;
  margin-bottom: 25px;
}

.modal-form__field:focus-within > .modal-form__label,
.modal-form__field:hover > .modal-form__label {
  transform: translateY(-38px) translateX(-38px) scale(0.8);
}

.modal-form__field:focus-within > .modal-form__input,
.modal-form__field:hover > .modal-form__input {
  border-color: #FF6C00;
}

.modal-form__field:focus-within > .modal-form__icon,
.modal-form__field:hover > .modal-form__icon {
  fill: #FF6C00;
}

.modal-form__field:focus-within > .modal-form__textarea,
.modal-form__field:hover > .modal-form__textarea {
  border-color: #FF6C00;
}

.modal-form__field:focus-within > .modal-form__comment-label,
.modal-form__field:hover > .modal-form__comment-label {
  transform: translateY(-45px) translateX(-8px) scale(0.8);
  border-color: #FF6C00;
}

.modal-form__input {
  width: 100%;
  display: block;
  height: 40px;
  padding: 0 0 0 42px;
  cursor: pointer;
  background-color: #FFFFFF;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: none;
  transition: border-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__input::placeholder {
  color: transparent;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__input:hover::placeholder,
.modal-form__input:focus::placeholder {
  color: rgba(47, 46, 47, 0.4);
}

.modal-form__input:not(:placeholder-shown) + .modal-form__label {
  transform: translateY(-38px) translateX(-38px) scale(0.8);
}

.modal-form__label {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 42px;
  font-size: 13px;
  line-height: 1.14;
  letter-spacing: 0.01em;
  color: #757575;
  text-transform: uppercase;
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__icon {
  position: absolute;
  display: block;
  top: 50%;
  left: 16px;
  transform: translateY(-50%);
  margin-right: 8px;
  fill: #757575;
  transition: fill 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__group {
  margin-bottom: 35px;
}

.modal-form__legend {
  font-size: 20px;
}

.modal-form__option-item {
  margin-bottom: 10px;
}

.modal-form__checkbox-label {
  display: inline-flex;
  align-items: center;
}

.modal-form__checkbox-label--policy {
  margin-bottom: 61px;
}

.modal-form__checkbox {
  appearance: none;
}

.modal-form__checkbox:checked + .modal-form__checkbox-icon {
  background-color: #FF6C00;
  background-image: url(../img/check.svg);
  background-size: contain;
  background-origin: border-box;
  border-color: #FF6C00;
}

.modal-form__checkbox-icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-right: 10px;
  border: 2px solid #2a2a2a;
  border-radius: 4px;
  background-color: #FFFFFF;
  transition: transform 250ms linear;
}

.modal-form__checkbox-icon.circle {
  border-radius: 50%;
}

.modal-form__city-select {
  margin-bottom: 45px;
}

.modal-form__select {
  height: 40px;
  padding: 0 0 0 10px;
  cursor: pointer;
  background-color: #FFFFFF;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: none;
  transition: border-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__select:hover,
.modal-form__select:focus {
  border-color: #FF6C00;
}

.modal-form__textarea {
  width: 100%;
  display: block;
  padding: 8px 4px;
  padding: 5px 0 0 10px;
  cursor: pointer;
  background-color: #FFFFFF;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;
  outline: none;
  resize: none;
  transition: border-color 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__textarea:hover::placeholder,
.modal-form__textarea:focus::placeholder {
  color: rgba(47, 46, 47, 0.4);
}

.modal-form__textarea::placeholder {
  color: transparent;
  transition: color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__textarea:not(:placeholder-shown) + .modal-form__comment-label {
  transform: translateY(-45px) translateX(-8px) scale(0.8);
  border-color: #FF6C00;
}

.modal-form__comment-label {
  position: absolute;
  top: 20px;
  transform: translateY(0);
  left: 10px;
  font-size: 13px;
  line-height: 1.14;
  letter-spacing: 0.01em;
  color: #757575;
  text-transform: uppercase;
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.modal-form__policy-link {
  text-decoration: none;
  outline: none;
  color: #FF6C00;
}

.mobile-menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 999;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 250ms linear, visibility 250ms linear;
}

.mobile-menu.is-open .mobile-menu__container {
  transform: translateX(0);
}

.mobile-menu__container {
  height: 100%;
  width: 70%;
  padding: 14px;
  background-color: #3e2b2b;
  transform: translateX(-100%);
  transition: transform 350ms linear 250ms, -webkit-transform 350ms linear 250ms;
}

.mobile-menu__inner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 25px;
  padding-bottom: 20px;
}

.mobile-menu__list {
  text-align: center;
}

.mobile-menu__item:not(:last-child) {
  margin-bottom: 20px;
}

.mobile-button {
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
}

